.input-switch[type="checkbox"] {
  height: 0;
  width: 0;
  visibility: hidden;
}

.lable-switch {
  cursor: pointer;
  text-indent: -9999px;
  width: 40px;
  height: 24px;
  background: linear-gradient(#4f2ee8 0%, #dcdcdc 100%);
  border-radius: 100px;
  display: block;
  position: relative;
  box-shadow: inset 1px 1px 2px rgba(0, 0, 0, .1);
}

.lable-switch:after {
  content: "";
  width: 18px;
  height: 18px;
  background: #fff;
  border-radius: 90px;
  transition: all .3s;
  position: absolute;
  top: 3px;
  bottom: 8.33%;
  left: 1px;
  right: 0%;
}

.input-switch:checked + .lable-switch {
  background: linear-gradient(#4f2ee8 0%, #686868 100%);
  box-shadow: inset 1px 1px 2px rgba(0, 0, 0, .1);
}

.input-switch:checked + .lable-switch:after {
  left: calc(100% - 1px);
  transform: translateX(-100%);
}

